import axios from "axios";

export const baseUrl = "https://music-api.heheda.top";

// axios
const axiosInstance = axios.create({
  baseURL: baseUrl
});

axiosInstance.interceptors.response.use(
  res => res.data,
  err => {
    console.log(err, "");
  }
);

export { axiosInstance };

